<template>
  <div>
    <div class="sub-view">
      <Header :title="''" :show="true" :bg="true" :border="true"></Header>
      <div class="container">
        <div ref="bd" @scroll="scroll" class="bd">
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">
            <p @click='goDetailMv(id+1,`${id+1}`)'>MV{{id+1}}</p>
          </div>
          <div class="info">
            <p @click='goDetailSinger(id+1,`${id+1}`)'>Singer{{id+1}}</p>
          </div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
          <div class="info">MV {{name}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/ComponentsLayout/Header/index'
export default {
  name: 'mv-detail',
  components: {
    Header
  },
  data () {
    return {
      scrollTop: 0,
      id: 0,
      name: ''
    }
  },
  activated () {
    this.$refs.bd.scrollTop = this.scrollTop
  },
  mounted () {
    this.id = this.$route.params.id
    this.name = this.$route.params.name
  },
  methods: {
    goDetailMv (index, name) {
      // 
      let newPath = `/movie/${index}`
      let newRoute = [{
        path: newPath,
        name: newPath,
        component: {extends: this.$router.extends.MovieDetail}
      }]
      // 
      let find = this.$router.options.routes.findIndex(item => {
        return item.path === newPath
      })
      //  
      if (find === -1) {
        this.$router.options.routes.push(newRoute[0])
        this.$router.addRoutes(newRoute)
      }
      // 
      this.$router.replace({
        name: newPath,
        params: { id: index, name: name }
      })
    },
    goDetailSinger (index, name) {
      // 
      let newPath = `/singer/${index}`
      let newRoute = [{
        path: newPath,
        name: newPath,
        component: {extends: this.$router.extends.SingerDetail}
      }]
      // 
      let find = this.$router.options.routes.findIndex(item => {
        return item.path === newPath
      })
      //  
      if (find === -1) {
        this.$router.options.routes.push(newRoute[0])
        this.$router.addRoutes(newRoute)
      }
      // 
      this.$router.replace({
        name: newPath,
        params: { id: index, name: name }
      })
    },
    scroll (e) {
      this.scrollTop = e.target.scrollTop
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../assets/css/mxin'
.container
  width 100%
  position absolute
  top 40px
  left 0
  bottom 0
  right 0
  .bd
    position absolute
    top 0
    left 0
    bottom 0
    right 0
    .info
      padding 10px
      font-size $fontM
      p
        font-size $fontS
        background #ccc;
        color #666666
        display inline-block
        line-height 30px
        height 30px
        border-radius 10px
        padding 0 10px
</style>



